import React, { useState, useEffect } from "react";

import "./social_card.css";
import Alert from "@mui/material/Alert";

const Social_Card = ({ type }) => {
  const [alertOpen, setalertOpen] = useState(false);

  let copyText = () => {
    navigator.clipboard.writeText("0vis#1008");
    setalertOpen(true);
    setTimeout(() => {
      setalertOpen(false);
    }, 2000);
  };

  return (
    <>
      <div className="social_card_content">
        <small className="text_small">Click to copy user</small>
        <div className="social_card" onClick={copyText}>
          <div className="social_card_left">
            <h1>CONTACT ME ON DISCORD</h1>
            <span>0vis#1008</span>
          </div>
          <div className="social_card_right">
            <Alert
              severity="success"
              className={alertOpen ? "alert_copied" : "alert_copied hidden"}
            >
              Copied to clipboard!
            </Alert>
            <i className="fab fa-discord"></i>
          </div>
        </div>
      </div>
    </>
  );
};

export default Social_Card;
